@charset "UTF-8";
/*Подключаем шрифты*/
/*Open Sans 400*/
@font-face {
  font-family: "Open Sans";
  font-style: normal;
  font-weight: 400;
  src: url("../fonts/opensans.woff2") format("woff2"), url("../fonts/opensans.woff") format("woff");
  font-display: swap;
}
/*Open Sans 700*/
@font-face {
  font-family: "Open Sans";
  font-style: normal;
  font-weight: 700;
  src: url("../fonts/opensansbold.woff2") format("woff2"), url("../fonts/opensansbold.woff") format("woff");
  font-display: swap;
}
/*Переменные для цветов*/
:root {
  --black: #000000;
  --white: #ffffff;
  --light-white: rgba(255, 255, 255, 0.9);
  --light-black: #231f20;
  --soft-black: rgba(0, 0, 0, 0.1);
  --easy-black: rgba(0, 0, 0, 0.3);
  --dark-grey: #444444;
  --easy-dark-grey: rgba(68, 68, 68, 0.1);
  --soft-dark-grey: rgba(68, 68, 68, 0.3);
  --extra-bark: rgba(0, 1, 1, 0.25);
  --green-blue: #63d1bb;
  --light-green-blue: #46c1ae;
  --soft-green-blue: rgba(70, 193, 174, 0.1);
  --moderate-cyan: #62d1ba;
  --light-gray: #b3b3b3;
  --light: #ececec;
  --extra-light: #f1f1f1;
  --super-light: #e5e5e5;
  --super-light-2: #e6e6e6;
  --super-light-3: #fafafa;
  --turquoise: #56c3b2;
  --silver: #c6c6c6;
}

*,
*::before,
*::after {
  box-sizing: border-box;
}

.visually-hidden {
  position: absolute;
  width: 1px;
  height: 1px;
  margin: -1px;
  padding: 0;
  border: 0;
  clip: rect(0 0 0 0);
  overflow: hidden;
}

.page {
  display: flex;
  font-family: "Open Sans", "Arial", sans-serif;
  font-size: 17px;
  line-height: 30px;
  font-weight: 400;
  color: var(--dark-grey);
  background-color: var(--white);
}

.page__body {
  width: 100%;
  margin: 0;
  padding: 0;
}

.main-header {
  display: flex;
  flex-direction: column;
  width: 100%;
  background-color: var(--white);
}

.main-header__container {
  box-shadow: 0 1px 0 var(--light), inset 0 1px 0 var(--light);
}

@media (min-width: 768px) {
  .main-header {
    margin: 0 auto;
    border-bottom: 1px solid var(--super-light-2);
  }

  .main-header__container {
    width: 100%;
    max-width: 768px;
    margin: auto;
    padding: 0 50px;
    box-shadow: none;
  }
}
@media (min-width: 1150px) {
  .main-header__container {
    width: 100%;
    max-width: 1150px;
    margin: 0 auto;
  }
}
.main-navigation {
  position: relative;
  line-height: 24px;
  font-weight: 700;
  color: var(--black);
}

.main-navigation__logo-link {
  display: block;
  min-height: 79px;
  text-align: center;
  text-decoration: none;
  box-shadow: 0 1px 0 var(--light), inset 0 1px 0 var(--light);
}

.main-navigation__logo-image {
  width: 86px;
  height: 79px;
}

.main-navigation__toggle {
  position: absolute;
  top: 29px;
  right: 30px;
  z-index: 2;
  display: block;
  width: 20px;
  height: 20px;
  padding: 0;
  background-color: transparent;
  border: none;
  cursor: pointer;
}

.main-navigation--closed .main-navigation__toggle::before {
  content: "";
  position: absolute;
  top: 3px;
  left: 0;
  width: 20px;
  height: 2px;
  background-color: var(--light-black);
  box-shadow: 0 4px 0 0 var(--light-black), 0 8px 0 0 var(--light-black);
}

.main-navigation--closed .main-navigation__toggle:hover::before {
  opacity: 0.6;
}

.main-navigation--closed .main-navigation__toggle:focus::before {
  opacity: 0.6;
  outline: none;
}

.main-navigation--closed .main-navigation__toggle:active::before {
  opacity: 0.3;
}

.main-navigation--opened .main-navigation__toggle::before, .main-navigation--opened .main-navigation__toggle::after {
  content: "";
  position: absolute;
  top: 7px;
  right: 0;
  width: 26px;
  height: 2px;
  background-color: var(--black);
}
.main-navigation--opened .main-navigation__toggle::before {
  transform: rotate(45deg);
  box-shadow: none;
}
.main-navigation--opened .main-navigation__toggle::after {
  transform: rotate(-45deg);
}
.main-navigation--opened .main-navigation__toggle:hover::before, .main-navigation--opened .main-navigation__toggle:hover::after {
  opacity: 0.6;
}
.main-navigation--opened .main-navigation__toggle:focus::before, .main-navigation--opened .main-navigation__toggle:focus::after {
  opacity: 0.6;
  outline: none;
}
.main-navigation--opened .main-navigation__toggle:active::before, .main-navigation--opened .main-navigation__toggle:active::after {
  opacity: 0.3;
}

.main-navigation--nojs .main-navigation__toggle {
  display: none;
}

.main-navigation__text {
  display: none;
  margin: 0;
  font-size: 15px;
  line-height: 24px;
  font-weight: 400;
  color: var(--dark-grey);
}

@media (max-width: 767px) {
  .main-navigation--closed .site-list__item {
    display: none;
  }

  .main-navigation--closed .user-list__item {
    display: none;
  }
}
@media (min-width: 768px) {
  .main-navigation {
    display: grid;
    grid-template-columns: 1fr 1fr;
  }

  .main-navigation__logo-link {
    text-align: start;
    box-shadow: none;
  }

  .main-navigation__logo-image {
    display: block;
    height: 89px;
    margin-right: auto;
  }

  .main-navigation__toggle {
    display: none;
  }

  .main-navigation__text {
    display: block;
    margin-left: auto;
    padding-top: 20px;
    order: 4;
  }
}
@media (min-width: 1150px) {
  .main-navigation {
    position: relative;
    display: flex;
    flex-wrap: wrap;
  }

  .main-navigation__logo-link {
    min-width: 150px;
    order: 2;
  }

  .main-navigation__logo-image {
    margin: auto;
  }

  .main-navigation__toggle {
    display: none;
  }

  .main-navigation__text {
    position: absolute;
    right: 0;
    bottom: 20px;
    padding: 0;
  }
}
