.button {
  display: block;
  padding: 16px;
  font-family: inherit;
  font-size: 17px;
  line-height: 20px;
  font-weight: 700;
  text-align: center;
  text-decoration: none;
  text-transform: uppercase;
  cursor: pointer;
}
.button--light {
  color: var(--dark-grey);
  background-color: var(--white);
  border: 2px solid var(--dark-grey);
}
.button--light:hover {
  color: var(--white);
  background-color: var(--dark-grey);
}
.button--light:focus {
  color: var(--white);
  background-color: var(--dark-grey);
  outline: none;
}
.button--light:active {
  color: var(--light-gray);
  background-color: var(--dark-grey);
}
.button--dark {
  color: var(--white);
  background-color: var(--green-blue);
  border: none;
}
.button--dark:hover {
  background-color: var(--turquoise);
}
.button--dark:focus {
  background-color: var(--turquoise);
  outline: none;
}
.button--dark:active {
  background-color: var(--turquoise);
  opacity: 0.7;
}

