}
.benefits__item--flower {
  margin-bottom: 30px;
  padding-top: 54px;
}
.benefits__item--flower::before {
  content: "";
  position: absolute;
  top: 0;
  left: 8px;
  width: 33px;
  height: 38px;
  background-image: url("../img/flower.svg");
}
.benefits__item--cash {
  margin-bottom: 35px;
  padding-top: 53px;
}
.benefits__item--cash::before {
  content: "";
