  top: 0;
  left: 7px;
  width: 34px;
  height: 27px;
  background-image: url("../img/heart.svg");
}
.benefits__item--threads {
  margin-bottom: 33px;
  padding-top: 51px;
}
.benefits__item--threads::before {
  content: "";
  position: absolute;
  top: 0;
  left: 3px;
  width: 35px;
  height: 34px;
  background-image: url("../img/threads.svg");
}
.benefits__item--rocket {
  margin-bottom: 32px;
  padding-top: 49px;
}
.benefits__item--rocket::before {
  content: "";
  position: absolute;
  top: 0;
  left: -6px;
  width: 55px;
  height: 23px;
  background-image: url("../img/rocket.svg");
}
.benefits__item--gift {
  padding-top: 52px;
}
.benefits__item--gift::before {
  content: "";
