.control {
  max-width: 260px;
  position: relative;
  padding-left: 60px;
}
.control:hover .control__mark {
  border-color: var(--moderate-cyan);
}
.control:focus .control__mark {
  border-color: var(--moderate-cyan);
}
.control:active .control__mark {
  border-color: var(--easy-black);
}

.control__input:disabled + .control__mark {
  border-color: var(--super-light-3);
}

.control__mark {
  position: absolute;
  top: -5px;
  left: 0;
  width: 42px;
  height: 42px;
  border: 2px solid var(--dark-grey);
}

.control__input[type=radio] + .control__mark {
  border-radius: 50%;
}

.control__input[type=checkbox]:checked + .control__mark::before {
  content: "";
  position: absolute;
  top: 11px;
  left: 8px;
  width: 23px;
  height: 16px;
  background-image: url("../img/check-mark.svg");
  background-repeat: no-repeat;
  background-size: cover;
}

.control__input[type=radio]:checked + .control__mark::before {
  content: "";
  position: absolute;
  top: 9px;
  left: 10px;
  width: 19px;
  height: 19px;
  background-color: var(--moderate-cyan);
  border-radius: 50%;
}

